/* eslint-disable no-unused-vars */
import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Modal,
} from 'reactstrap';

import AddCustomQuestionModal from '../../../../../components/application/AddQuestionModal';

import IntlMessages from '../../../../../helpers/IntlMessages';

const StepTwo = ({ formValues, setFormValues }) => {
  const [openAddModal, setOpenAddModal] = useState(false);

  const handleChange = (data, key) => {
    const checkedItems = [...formValues.application[key]];
    const index = checkedItems.indexOf(data);
    if (index === -1) {
      checkedItems.push(data);
    } else {
      checkedItems.splice(index, 1);
    }
    setFormValues({
      ...formValues,
    });
  };

  return (
    <div className="wizard-basic-step">
      <Card className="mb-4">
        <CardBody>
          <CardTitle>
            <div className="lead h4">
              <IntlMessages id="challenges.add-question-title" />
            </div>
          </CardTitle>
          <FormGroup>
            <Button className="m-2" onClick={() => setOpenAddModal(true)}>
              <IntlMessages id="challenges.add-custom-question" />
            </Button>
          </FormGroup>
          <Modal
            isOpen={openAddModal}
            toggle={() => setOpenAddModal(!openAddModal)}
          >
            <AddCustomQuestionModal
              setOpenAddModal={setOpenAddModal}
              formValues={formValues}
              setFormValues={setFormValues}
            />
          </Modal>{' '}
        </CardBody>
      </Card>
    </div>
  );
};

export default StepTwo;
