/* eslint-disable prefer-promise-reject-errors */
import React from 'react';
import { Card, CardBody, CardTitle, FormGroup } from 'reactstrap';
import StateButton from '../../../../../components/StateButton';
import instance from '../../../../../helpers/axiosInstance';
import IntlMessages from '../../../../../helpers/IntlMessages';

const FinalStep = ({ formValues, setFormValues }) => {
  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const updateProfile = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/updateHackathon',
            data: { ...newData },
          });

          const { data } = response;

          if (data.success) {
            setFormValues({ ...formValues, ...newData });
            success('Hackathon updated successfully!');
          } else {
            failed('Failed to update hackathon!');
          }
        } catch (error) {
          console.log(error);
          failed('Failed to update hackathon!');
        }
      };
      updateProfile();
    });
  };

  return (
    <div className="wizard-basic-step">
      <Card className="mb-4">
        <CardBody>
          <CardTitle>
            <div className="lead h4">
              <IntlMessages id="challenges.submit-application" />
            </div>
          </CardTitle>

          <FormGroup className="text-center">
            <StateButton
              id="submit-application"
              color="primary"
              type="submit"
              onClick={() => handleSubmit(formValues)}
            >
              <IntlMessages id="forms.submit" />
            </StateButton>
          </FormGroup>
        </CardBody>
      </Card>
    </div>
  );
};

export default FinalStep;
