import React from 'react';
import { Button, Card, CardBody, CardTitle, FormGroup } from 'reactstrap';

import IntlMessages from '../../../../../helpers/IntlMessages';

const StepOne = ({ formValues, setFormValues }) => {
  const handleChange = (data, key) => {
    const checkedItems = [...formValues.application[key]];
    const index = checkedItems.indexOf(data);
    if (index === -1) {
      checkedItems.push(data);
    } else {
      checkedItems.splice(index, 1);
    }
    setFormValues({
      ...formValues,
      application: { ...formValues.application, [key]: checkedItems },
    });
  };

  return (
    <div className="wizard-basic-step">
      <Card className="mb-4">
        <CardBody>
          <CardTitle>
            <div className="lead h4">
              <IntlMessages id="challenges.about-title" />
            </div>
          </CardTitle>

          <FormGroup className="mb-6">
            <Button
              id="first-and-last-name"
              className="m-2"
              color={
                formValues.application.about.includes(1) ? 'success' : 'primary'
              }
              onClick={() => handleChange(1, 'about')}
            >
              <IntlMessages id="challenges.about.first-and-last-name" />
            </Button>

            <Button
              id="bio"
              className="m-2"
              color={
                formValues.application.about.includes(2) ? 'success' : 'primary'
              }
              onClick={() => handleChange(2, 'about')}
            >
              <IntlMessages id="challenges.about.bio" />
            </Button>

            <Button
              id="gender"
              className="m-2"
              color={
                formValues.application.about.includes(3) ? 'success' : 'primary'
              }
              onClick={() => handleChange(3, 'about')}
            >
              <IntlMessages id="challenges.about.gender" />
            </Button>
            <Button
              id="dob"
              className="m-2"
              color={
                formValues.application.about.includes(4) ? 'success' : 'primary'
              }
              onClick={() => handleChange(4, 'about')}
            >
              <IntlMessages id="challenges.about.dob" />
            </Button>
            <Button
              id="tshirt-size"
              className="m-1"
              color={
                formValues.application.about.includes(5) ? 'success' : 'primary'
              }
              onClick={() => handleChange(5, 'about')}
            >
              <IntlMessages id="challenges.about.tshirt-size" />
            </Button>
          </FormGroup>
        </CardBody>
      </Card>

      <Card className="mb-4">
        <CardBody>
          <CardTitle>
            <div className="lead h4">
              <IntlMessages id="challenges.education-title" />
            </div>
          </CardTitle>

          <FormGroup className="mb-6">
            <Button
              id="education-details"
              className="m-2"
              color={
                formValues.application.education.includes(1)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(1, 'education')}
              active={formValues.application.education.includes(1)}
            >
              <IntlMessages id="challenges.education.education-details" />
            </Button>
          </FormGroup>
        </CardBody>
      </Card>

      <Card className="mb-4">
        <CardBody>
          <CardTitle>
            <div className="lead h4">
              <IntlMessages id="challenges.experience-title" />
            </div>
          </CardTitle>
          <FormGroup>
            <Button
              id="domain-expertise"
              className="m-2"
              color={
                formValues.application.experience.includes(1)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(1, 'experience')}
              active={formValues.application.experience.includes(1)}
            >
              <IntlMessages id="challenges.experience.domain-expertise" />
            </Button>
            <Button
              id="skills"
              className="m-2"
              color={
                formValues.application.experience.includes(2)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(2, 'experience')}
              active={formValues.application.experience.includes(2)}
            >
              <IntlMessages id="challenges.experience.skills" />
            </Button>
            <Button
              id="resume"
              className="m-2"
              color={
                formValues.application.experience.includes(3)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(3, 'experience')}
              active={formValues.application.experience.includes(3)}
            >
              <IntlMessages id="challenges.experience.resume" />
            </Button>
            <Button
              id="work-experience"
              className="m-1"
              color={
                formValues.application.experience.includes(4)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(4, 'experience')}
              active={formValues.application.experience.includes(4)}
            >
              <IntlMessages id="challenges.experience.work-experience" />
            </Button>
          </FormGroup>
        </CardBody>
      </Card>

      <Card className="mb-4">
        <CardBody>
          <CardTitle>
            <div className="lead h4">
              <IntlMessages id="challenges.online-profiles-title" />
            </div>
          </CardTitle>

          <FormGroup className="mb-6">
            <Button
              id="github"
              className="m-2"
              color={
                formValues.application.online_profiles.includes(1)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(1, 'online_profiles')}
              active={formValues.application.online_profiles.includes(1)}
            >
              <IntlMessages id="challenges.online-profiles.github" />
            </Button>
            <Button
              id="dribble"
              className="m-2"
              color={
                formValues.application.online_profiles.includes(2)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(2, 'online_profiles')}
              active={formValues.application.online_profiles.includes(2)}
            >
              <IntlMessages id="challenges.online-profiles.dribble" />
            </Button>
            <Button
              id="behance"
              className="m-2"
              color={
                formValues.application.online_profiles.includes(3)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(3, 'online_profiles')}
              active={formValues.application.online_profiles.includes(3)}
            >
              <IntlMessages id="challenges.online-profiles.behance" />
            </Button>
            <Button
              id="stack-overflow"
              className="m-2"
              color={
                formValues.application.online_profiles.includes(4)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(4, 'online_profiles')}
              active={formValues.application.online_profiles.includes(4)}
            >
              <IntlMessages id="challenges.online-profiles.stack-overflow" />
            </Button>
            <Button
              id="linkedin"
              className="m-1"
              color={
                formValues.application.online_profiles.includes(5)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(5, 'online_profiles')}
              active={formValues.application.online_profiles.includes(5)}
            >
              <IntlMessages id="challenges.online-profiles.linkedin" />
            </Button>
            <Button
              id="facebook"
              className="m-1"
              color={
                formValues.application.online_profiles.includes(6)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(6, 'online_profiles')}
              active={formValues.application.online_profiles.includes(6)}
            >
              <IntlMessages id="challenges.online-profiles.facebook" />
            </Button>
            <Button
              id="twitter"
              className="m-1"
              color={
                formValues.application.online_profiles.includes(7)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(7, 'online_profiles')}
              active={formValues.application.online_profiles.includes(7)}
            >
              <IntlMessages id="challenges.online-profiles.twitter" />
            </Button>
          </FormGroup>
        </CardBody>
      </Card>

      <Card className="mb-4">
        <CardBody>
          <CardTitle>
            <div className="lead h4">
              <IntlMessages id="challenges.contact-title" />
            </div>
          </CardTitle>

          <FormGroup className="mb-6">
            <Button
              id="phone-number"
              className="m-2"
              color={
                formValues.application.contact.includes(1)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(1, 'contact')}
              active={formValues.application.contact.includes(1)}
            >
              <IntlMessages id="challenges.contact.phone-number" />
            </Button>
            <Button
              id="email"
              className="m-2"
              color={
                formValues.application.contact.includes(2)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(2, 'contact')}
              active={formValues.application.contact.includes(2)}
            >
              <IntlMessages id="challenges.contact.email" />
            </Button>
            <Button
              id="city"
              className="m-2"
              color={
                formValues.application.contact.includes(3)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(3, 'contact')}
              active={formValues.application.contact.includes(3)}
            >
              <IntlMessages id="challenges.contact.city" />
            </Button>
            <Button
              id="emergency-contact"
              className="m-2"
              color={
                formValues.application.contact.includes(4)
                  ? 'success'
                  : 'primary'
              }
              onClick={() => handleChange(4, 'contact')}
              active={formValues.application.contact.includes(4)}
            >
              <IntlMessages id="challenges.contact.emergency-contact" />
            </Button>
          </FormGroup>
        </CardBody>
      </Card>
    </div>
  );
};

export default StepOne;
